.navbar {
  padding: 0 20px;
  display: flex;
}

.navbar h1 {
  flex: 2;
  font-family: kazimir, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 18px;
  margin-top: 20px;
}

.navbar ul {
  list-style: none;
}

.navbar li {
  /* justify-content: flex-end; */

  float: right;
  padding: 0px 20px;
  font-size: 12px;
  font-family: brandon-grotesque, sans-serif;
  font-style: normal;
  font-weight: 100;
  text-transform: uppercase;
  margin-top: 20px;
}

.navbar a {
  color: #306b34;
}

.navbar a:hover {
  color: black;
}
